<template>
<div id = "notificationCenter-root"> 
<div class = "noti" v-for = "file in notis">
<div class = "icon"> </div>
<div class = "title">{{file.name}} failed to load! </div>

</div>

</div>
</template>

<script>
export default{
    name : 'notificationCenter',
    data () {
        return {
            notis : [
                {name: "obj.obj", msg : "didnt work lol"}
            ]
        }
    }
}

</script>

<style lang = "stylus">
#notificationCenter-root{
    position absolute
    left 175px
    top 12px
    width 410px
    height 100vh
    background-color rgba(1,1,1,0)
    box-sizing border-box
    padding-top 20px

    .noti{
        width 100%
        height 50px
        line-height 30px
        background-color white
        padding 10px 10px
        box-sizing border-box

        .icon{
            width 30px
            height 30px
            margin-right 15px
            float left
            background-color red
        }

        .title{
            font-weight bold
        }
    }
}

</style>


